export const addProduct = (name, category, quantity, weight, description ) => ({
  type: 'ADD_PRODUCT',
  name, 
  category, 
  quantity, 
  weight, 
  description
})

export const editProduct = (id, name, category, quantity, weight, description ) => ({
  type: 'EDIT_PRODUCT',
  id,
  name, 
  category, 
  quantity, 
  weight, 
  description
})