import React from 'react';
import ProductCard from './ProductCard';
import styled from '@emotion/styled';
import { Link } from "react-router-dom";


const ProductGrid = styled.div`
    display: flex;
    margin-left: -20px;
    flex-wrap: wrap;
    & > * {
        margin-left: 20px;
    }
`
const NiceLink = styled(Link)`
    text-decoration: none;
` 

function ProductList({ products, onProductHover }) {
  const events = {
    onProductHover
  };

  return (
    <ProductGrid>
      {products.map(function(product){ 
      const productUrl = `/food-product-catalog/edit-product/${product.id}`
      return (
      <NiceLink key={product.id} to={productUrl}>
       <ProductCard product={product} {...events} /></NiceLink>
      )
  })}
    </ProductGrid>
  );
}

export default ProductList;
