import React, { Component } from 'react';
import ProductListContainer from './containers/ProductListContainer'
import AddProductContainer from "./containers/AddProductContainer";
import { BrowserRouter as Router, Route } from "react-router-dom";
import PageFrame from "./components/PageFrame";

class App extends Component {
  render() {
    return (
      <Router>
        <PageFrame>
        {process.env.NODE_ENV === 'development' && 
        <Route exact path="/" component={ProductListContainer} /> 
        }
       <Route exact path="/food-product-catalog" component={ProductListContainer} /> 
       <Route path="/food-product-catalog/add-product" component={AddProductContainer} />
       <Route path="/food-product-catalog/edit-product/:id" component={AddProductContainer} />
        
       </PageFrame>
      </Router>      
    );
  }
}

export default App;
